				<view class="">{{cateLabel}}/{{areaLabel}}</view>
				
				<u-icon name="arrow-down" color="#333" size="20"></u-icon>
			</view>
			<view class="search_right">
				<u--input
				    placeholder="请输入内容"
				    border="none"
